import React from 'react'
import Subble from './subble'
import Link from 'gatsby-link'
import './subble.scss'

const NavBar = () => (
  <Subble stylingClass="navBarSubble">
    <Subble stylingClass = "navBarItemSubble"><Link to = "/">Home</Link></Subble>
    <Subble stylingClass = "navBarItemSubble"><Link to = "/about">About</Link></Subble>
    <Subble stylingClass = "navBarItemSubble"><Link to = "/projects">Projects</Link></Subble>
    <Subble stylingClass = "navBarItemSubble"><Link to = "/contact">Contact</Link></Subble>
  </Subble>
)

export default NavBar
